body {
  margin: 0;
}

.App {
  font-family: sans-serif;
  text-align: center;
  background-color: #f1f1f1;
  height: 100vh;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App > div {
  background-color: #fff;
  padding: 16px;
  border-radius: 20px;
  min-height: 250px;
  height: max-content;
}

.input-area {
  position: relative;
}

input {
  background-color: #f1f1f1;
  border-radius: 20px;
  padding: 16px;
  border: none;
  font-size: 16px;
  outline: none;
  z-index: 3;
  font-weight: 300;
}

input + .ghost-input {
  position: absolute;
  left: 16px;
  top: 16px;
  z-index: 2;
  font-weight: 300;
}

.data-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  width: max-content;
}

.data-wrapper > div {
  position: relative;
}
